<template>
  <div class="hello">
<!--    <h1>LV Hologram</h1>-->
    <img :src="getImageSrc(path)" alt="lv-hologram">
  </div>
</template>


<script>
export default {
  /* eslint-disable */
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      path: '',
      currentNumber: null,
      reset: false
    }
  },
  mounted() {
    this.setImageSrc();
  },
  watch: {
    reset(val) {
      if (val) {
        this.setImageSrc()
      }
    }
  },
  methods: {
    lpad(value, padding = 3) {
      const zeroes = new Array(padding+1).join("0");

      return (zeroes + value).slice(-padding);
    },
    setImageSrc() {
      // const path = '../assets/mini/';
      const baseName = 'imageN93372_360';
      const extension = '.png';
      const numberOfImages = 360;
      const spacing = 2;

      this.reset = false;

      for (let i = 0; i < numberOfImages; i = i + 3) {
        setTimeout(() => {
          this.currentNumber = this.lpad(i);
          this.path = `${baseName}_${this.currentNumber}${extension}`
          // this.path = `${path}${baseName}_${this.currentNumber}${extension}`

          if (i === (numberOfImages - (spacing + 1))) {
            this.reset = true;
          }
        }, i * 14)
      }

    },
    getImageSrc(path) {
      return require(`../assets/mini/${path}`);
    }
  }
}
</script>

